@mixin width-procent($widthPx, $widthFull) {
	width: (($widthPx / $widthFull) * 100%)+0.35; }
.contacts {
	height: 747px;
	& .fix-container {
		@include width-procent(1170, 1920);
		& .contacts__content {
			display: flex;
			justify-content: space-between;
			& .contacts__map {}
			& .contacts__map iframe {}
			& .contacts__information {
				height: 540px;
				width: 370px;
				background-color: #f5f5f5;
				padding: 40px;
				& .contacts__item {
					display: flex;
					flex-direction: column;
					margin: 0 0 20px 0;
					& h5[class*="item__title"] {
						font-size: 16px;
						font-weight: 700;
						line-height: 30px;
						letter-spacing: 0.48px;
						text-transform: uppercase;
						margin: 0 0 10px 0; }
					& .item__title_gray {
						max-height: 25px;
						color: rgba(17,18,39,.4);
						& a {
							color: rgba(17,18,39,.4); } }
					& .item__title_bg-color {
						background-color: #0e539a;
						color: #fff;
						width: fit-content;
						padding: 0px 10px; }
					& .item__subtitle_bold {
						max-height: 28px;
						color: #111227;
						font-size: 30px;
						font-weight: 600;
						line-height: 30px;
						letter-spacing: 0.9px;
						& a {
							color: #111227; } }
					& .item__subtitle_small {
						color: #111227;
						font-size: 17px;
						font-weight: 300;
						line-height: 24px;
						letter-spacing: 0.54px;
						& a {
							color: #111227; } } } } } } }
