$maxWidth: 1920;

@mixin adaptiv-font($pcSize, $mobSize) {
	$addSize: $pcSize - $mobSize;
	$maxWidth: $maxWidth - 320;
	font-size: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth})); }

@mixin width-procent($widthPx, $widthFull) {
	width: (($widthPx / $widthFull) * 100%)+0.35; }

.footer {
	height: 238px;
	& .footer__navigation {
		height: 59px;
		background-color: #0e539a;
		& .footer__fix-container {
			display: flex;
			align-items: center;
			justify-content: center;
			& .footer__nav-list {
				@include width-procent(873, 1370);
				display: flex;
				justify-content: space-between;
				& .nav-list__item {
					font-size: 18px;
					font-weight: 700;
					line-height: 24px;
					text-transform: uppercase;
					letter-spacing: 0.54px;
					& a {
						color: #ffffff; } } } } }
	& .footer__contacts {
		height: 176px;
		background-color: #222222;
		& .footer__fix-container {
			& .footer__contacts-information {
				@include width-procent(1168, 1370);
				display: flex;
				justify-content: space-between;
				padding: 25px 0 0 0;
				margin: 0 auto;
				& .footer__title {
					font-size: 18px;
					font-weight: 700;
					line-height: 24px;
					letter-spacing: 0.54px;
					color: #ffffff;
					margin: 0 0 5px 0; }
				& *[class*="subtitle"] {
					font-size: 16px;
					font-weight: 500;
					line-height: 24px;
					letter-spacing: 0.48px;
					color: #ffffff; }
				& .footer__address {
					width: 24.93394%;
					& .address__subtitle {} }
				& .footer__work-hours {
					width: 15.70766%;
					& .work-hours__subtitle {} }
				& .footer__phone-contacts {
					width: 17.43759%;
					display: flex;
					flex-direction: column;
					& .phone-contacts__subtitle {}
					& .footer__chats {
						height: 20px;
						margin: 10px 0 0 0;
						& .chats__item {
							height: 20px;
							width: 20px; }
						& .telegram {}
						& .viber {
							margin: 0 12px; }
						& .whatsapp {} } }
				& .footer__payments {
					width: 30.77336%;
					& .footer__banks-logo {
						margin: 8px 0 0 0;
						& .payments__item {
							margin: 0 15px 0 0; }
						& .vtb, .sber {
							margin: 0; }
						& .alfa {
							margin: 18px 15px 0 0; } } } }
			& .footer__copyright {
				width: 85.60547%;
				margin: 0 auto;
				font-size: 14px;
				font-weight: 500;
				line-height: 20px;
				letter-spacing: 1.7px;
				color: #ffffff; } } } }
