$maxWidth: 1920;

@mixin adaptiv-font($pcSize, $mobSize) {
	$addSize: $pcSize - $mobSize;
	$maxWidth: $maxWidth - 320;
	font-size: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth})); }

@mixin width-procent($widthPx, $widthFull) {
	width: (($widthPx / $widthFull) * 100%)+0.35; }

.news-block {
	margin: 40px 0 95px 0;
	& .news-block__fix-container {
		@include width-procent(1170, 1920);
		& .news-block__news {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			margin: 0 0 19px 0;
			& .news-block__item {
				@include width-procent(370, 1170);
				margin: 0 0 40px 0;
				min-width: 370px;
				& .item__img {}
				& .item__date {
					color: #0e429a;
					font-size: 14px;
					font-weight: 600;
					line-height: 40px;
					letter-spacing: 0.42px; }
				& .item__title {
					color: #111227;
					font-size: 20px;
					font-weight: 700;
					line-height: 1.3;
					letter-spacing: 0.6px;
					margin: 0 0 14px 0; }
				& .item__article {
					opacity: 0.8;
					color: #05080c;
					font-size: 14px;
					font-weight: 500;
					line-height: 24px;
					letter-spacing: 0.14px; } } }
		& .news-block__pagination {
			display: flex;
			align-items: center;
			width: fit-content;
			margin: 0 auto;
			& .arrow {
				width: 15px;
				height: 13px; }
			& .pagination__prev {
				margin: 0 10px 0 0; }
			& .pagination__next {
				transform: rotate(180deg); }
			& .pagination__page-num {
				display: flex;
				& .page-num__item {
					color: #111227;
					font-size: 18px;
					font-weight: 600;
					line-height: 40px;
					letter-spacing: 0.54px;
					letter-spacing: 0.54px;
					margin: 0 10px 0 0; }
				& .page-num__item.active {
					text-align: center;
					background-color: #0e539a;
					width: 38px;
					height: 37px;
					border-radius: 20px;
					color: #fff; } } } } }
