$maxWidth: 1920;

@mixin adaptiv-font($pcSize, $mobSize) {
	$addSize: $pcSize - $mobSize;
	$maxWidth: $maxWidth - 320;
	font-size: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth})); }

@mixin width-procent($widthPx, $widthFull) {
	width: (($widthPx / $widthFull) * 100%)+0.35; }

.avail {
	margin: 0 0 93px 0;
	& .avail__fix-container {
		@include width-procent(1170, 1920);
		& .avail__tumbler {
			& .tumbler__item {
				padding: 10px 20px;
				color: #111227;
				font-size: 26px;
				font-weight: 400;
				line-height: 28px;
				letter-spacing: 0.78px;
				text-transform: uppercase;
				background-color: transparent;
				margin: 0 50px 0 0; }
			& .tumbler__item.active {
				background-color: #28bd49;
				color: #ffffff; } }
		& .avail__content {
			margin: 100px 0 0 0;
			& .content__img {}
			& .content__item {
				& .item__title {
					color: #111227;
					font-size: 26px;
					font-weight: 700;
					line-height: 32px;
					text-transform: uppercase;
					text-align: center;
					margin: 47px 0 27px 0; }
				& .item__subtitle {
					color: #454551;
					font-size: 20px;
					font-weight: 400;
					line-height: 28px;
					letter-spacing: 0.2px;
					text-align: center; }
				& .item_avails {
					display: flex;
					flex-wrap: wrap;
					justify-content: space-between;
					margin: 52px 0 0 0;
					& .avails__item {
						@include width-procent(242, 1170);
						color: #111227;
						font-size: 16px;
						font-weight: 600;
						line-height: 24.01px;
						position: relative;
						margin: 0 0 38px 0;
						&:before {
							content: url('/img/avail__img2.png');
							position: absolute;
							left: -10%;
							top: 3%; } } }
				& .content__article {
					@include width-procent(970, 1170);
					font-weight: 500;
					line-height: 24px;
					letter-spacing: 0.16px;
					font-size: 19px;
					letter-spacing: 0.19px;
					color: #05080c;
					padding: 25px;
					text-align: center;
					margin: 0 auto; }
				& .content__article.bg-color {
					font-size: 16px;
					font-weight: 400;
					color: #ffffff;
					background-color: #0e539a; } } } } }
