$maxWidth: 1920;

@mixin adaptiv-font($pcSize, $mobSize) {
	$addSize: $pcSize - $mobSize;
	$maxWidth: $maxWidth - 320;
	font-size: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth})); }

@mixin width-procent($widthPx, $widthFull) {
	width: (($widthPx / $widthFull) * 100%)+0.35; }

.offer {
	min-height: 589px;
	height: fit-content;
	margin: 40px 0;
	& .offer__fix-container {
		display: flex;
		justify-content: space-between;
		@include width-procent(1170, 1920);
		& .offer__col-left {
			@include width-procent(730, 1170);
			& .offer__title {
				color: #111227;
				font-size: 26px;
				font-weight: 700;
				line-height: 40px;
				text-transform: uppercase;
				margin: 0 0 25px 0; }
			& .offer__text-block {
				margin: 0 0 20px 0;
				& .offer__article-name {
					height: 52px;
					background-color: #f5f5f5;
					display: flex;
					align-items: center;
					padding: 0 0 0 5%;
					position: relative;
					& .text-block__toggle-btn {
						position: absolute;
						top: 50%;
						right: 20px;
						background: transparent;
						width: 15px;
						height: 9px;
						border-left: 7.5px solid transparent;
						border-right: 7.5px solid transparent;
						border-bottom: 9px solid rgba(17,18,39,.4);
						transform: translate(0, -50%) rotate(180deg); }
					& .article-name__item {
						color: #111227;
						font-size: 18px;
						font-weight: 400;
						line-height: 30px;
						letter-spacing: 0.54px; } }
				& .offer__article {
					display: none;
					color: #05080c;
					font-size: 16px;
					font-weight: 400;
					line-height: 28px;
					letter-spacing: 0.16px;
					padding: 20px 40px 20px 20px; } }
			& .offer__text-block.active {
				& .offer__article-name {
					background-color: #0e539a;
					& .text-block__toggle-btn {
						border-bottom: 9px solid #fff;
						transform: translate(0, -50%) rotate(0deg); }
					& .article-name__item {
						color: #ffffff; } }
				& .offer__article {
					display: block; } } }
		& .offer__col-right {
			@include width-procent(375, 1170);
			display: flex;
			justify-content: center;
			align-items: center;
			& .col-right__img3 {} } } }
