$maxWidth: 1920;

@mixin adaptiv-font($pcSize, $mobSize) {
	$addSize: $pcSize - $mobSize;
	$maxWidth: $maxWidth - 320;
	font-size: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth})); }

@mixin width-procent($widthPx, $widthFull) {
	width: (($widthPx / $widthFull) * 100%)+0.35; }

.promo-block {
	& .promo-block__fix-container {
		@include width-procent(1170, 1920);
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		& .promo-block__col-left {
			@include width-procent(868, 1170);
			& .col-left__img {}
			& .col-left__date {
				color: #0e429a;
				font-size: 14px;
				font-weight: 600;
				line-height: 40px;
				letter-spacing: 0.42px; }
			& .col-left__title {
				color: #111227;
				font-size: 24px;
				font-weight: 700;
				line-height: 40px;
				letter-spacing: 0.72px; }
			& .col-left__article {
				opacity: 0.8;
				color: #05080c;
				font-size: 14px;
				font-weight: 500;
				line-height: 24px;
				letter-spacing: 0.14px; } }
		& .promo-block__col-right {
			@include width-procent(270, 1170);
			position: relative;
			& .promo-block__mask {
				background-image: linear-gradient(to top, #0e539a, transparent);
				z-index: 2;
				width: 100%;
				height: 100%;
				position: absolute;
				top: 0;
				left: 0; }
			& .col-right__img {
				width: 100%; }
			& .col-right__desription {
				width: 100%;
				position: absolute;
				bottom: 30px;
				left: 20px;
				z-index: 3;
				& .desription__date {
					color: #ffffff;
					font-size: 14px;
					font-weight: 600;
					line-height: 40px;
					letter-spacing: 0.42px; }
				& .description__title {
					color: #ffffff;
					font-size: 20px;
					font-weight: 700;
					line-height: 28px;
					letter-spacing: 0.6px; } } } } }
