$maxWidth: 1920;

@mixin adaptiv-font($pcSize, $mobSize) {
	$addSize: $pcSize - $mobSize;
	$maxWidth: $maxWidth - 320;
	font-size: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth})); }

@mixin width-procent($widthPx, $widthFull) {
	width: (($widthPx / $widthFull) * 100%)+0.35; }

.car-info {
	margin: 0 0 70px 0;
	& .car-info__img {
		width: 100%;
		height: 379px; }
	& .car-info__slider {
		position: relative;
		height: 137px;
		display: flex;
		margin: 10px 0 0 0;
		& .slider__item {
			@include width-procent(230, 770);
			height: 100%;
			margin: 0 10px 0 0; }
		& .slider__btn-next {
			width: 50px;
			height: 100%;
			background-color: #28bd49;
			position: absolute;
			top: 0;
			right: 0;
			display: flex;
			align-items: center;
			justify-content: center; } }
	& .car-info__details {
		display: flex;
		justify-content: space-between;
		margin: 30px 0 0 0;
		& .car-info__characteristics {
			@include width-procent(160, 770);
			& .characteristics__title {
				color: #111227;
				font-size: 20px;
				font-weight: 700;
				line-height: 40px;
				letter-spacing: 0.6px; }
			& .characteristics__list {
				& .list__item {
					color: #4a4a4a;
					font-size: 16px;
					font-weight: 500;
					line-height: 36px;
					& .item__name {}
					& .item__argument {
						font-weight: 700; } } } }
		& .car-info__equipment {
			@include width-procent(160, 770);
			& .equipment__title {
				color: #111227;
				font-size: 20px;
				font-weight: 700;
				line-height: 40px;
				letter-spacing: 0.6px; }
			& .equipment__list {
				& .list__item {
					color: #111227;
					font-size: 16px;
					font-weight: 500;
					line-height: 40px;
					letter-spacing: 0.48px;
					display: flex;
					align-items: center; }
				& .item__img {
					height: 20px;
					width: 20px;
					margin: 0 5px 0 0; } } }
		& .car-info__conditions {
			@include width-procent(366, 770);
			& .conditions__title {
				color: #111227;
				font-size: 20px;
				font-weight: 700;
				line-height: 40px;
				letter-spacing: 0.6px; }
			& .conditions__list {
				& .list__item.article {
					font-weight: 700;
					letter-spacing: 0.48px;
					line-height: 18px; }
				& .list__item {
					font-size: 16px;
					color: #4a4a4a;
					margin: 0 0 32px 0;
					& .item__name {
						font-weight: 500; }
					& .item__argument {
						font-weight: 700; }
					& .item__fake-link {
						color: #0e539a;
						font-weight: 700;
						letter-spacing: 0.48px;
						line-height: 22px;
						text-decoration: underline; } } } } }
	& .car-info__desription {
		margin: 40px 0 0 0;
		& .desription__title {
			color: #ffffff;
			font-size: 24px;
			font-weight: 400;
			line-height: 40px;
			letter-spacing: 0.72px;
			width: fit-content;
			padding: 0 15px;
			background-color: #0e539a;
			margin: 0 0 30px 0; }
		& .desription__article {
			color: #111227;
			font-size: 16px;
			font-weight: 500;
			line-height: 28px;
			letter-spacing: 0.48px; } }
	& .car-info__reviews {
		margin: 40px 0 0 0;
		& .reviews__title {
			color: #ffffff;
			font-size: 24px;
			font-weight: 400;
			line-height: 40px;
			letter-spacing: 0.72px;
			width: fit-content;
			padding: 0 15px;
			background-color: #0e539a;
			margin: 0 0 30px 0; }
		& .reviews__comment {
			display: flex;
			justify-content: space-between;
			& .comment__photo {
				@include width-procent(70, 770);
				& .photo__item {
					width: 100%; } }
			& .comment__text {
				@include width-procent(677, 770);
				display: flex;
				flex-direction: column;
				margin: 0 0 30px 0;
				& .text__name {
					color: #111227;
					font-size: 18px;
					font-weight: 700;
					line-height: 40px;
					letter-spacing: 0.54px; }
				& .text__date {
					opacity: 0.8;
					color: #111227;
					font-size: 14px;
					font-weight: 400;
					letter-spacing: 0.42px;
					margin: 0 0 10px 0; }
				& .text__article {
					color: #111227;
					font-size: 16px;
					font-weight: 400;
					line-height: 24px;
					letter-spacing: 0.16px; } }
			& .comment__reply {
				@include width-procent(649, 677);
				border-left: 2px solid #0e539a;
				padding: 5px 0 5px 15px;
				margin: 20px 0 0 0;
				& .reply__admin {
					display: flex;
					align-items: center;
					& .admin__img {
						margin: 0 15px 0 0; }
					& .admin__name {
						color: #111227;
						font-size: 18px;
						font-weight: 700;
						line-height: 40px;
						letter-spacing: 0.54px; } }
				& .reply__article {
					color: #111227;
					font-size: 16px;
					font-weight: 600;
					line-height: 24px;
					letter-spacing: 0.16px; } } }
		& .reviews__put-review {
			color: #ffffff;
			font-size: 21px;
			font-weight: 400;
			line-height: 14.96px;
			letter-spacing: 0.83px;
			background-color: #28bd49;
			padding: 15px 20px; } } }
