$maxWidth: 1920;

@mixin adaptiv-font($pcSize, $mobSize) {
	$addSize: $pcSize - $mobSize;
	$maxWidth: $maxWidth - 320;
	font-size: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth})); }

@mixin width-procent($widthPx, $widthFull) {
	width: (($widthPx / $widthFull) * 100%)+0.35; }

.rent {
	& .rent__fix-container {
		@include width-procent(1170, 1920);
		& .fix-container__img {}
		& .rent__text-block {
			height: 226px;
			display: flex;
			margin: 20px 0;
			& .rent__col-left {
				width: 50%;
				& .col-left__title {
					color: #111227;
					font-size: 26px;
					font-weight: 700;
					line-height: 32px;
					text-transform: uppercase; } }
			& .rent__col-right {
				width: 50%;
				& .col-right__article {
					font-size: 18px;
					font-weight: 400;
					line-height: 28px;
					letter-spacing: 0.54px;
					color: #05080c; } } } } }





acive {
	background: transparent;
	width: 15px;
	height: 9px;
	border-left: 7.5px solid transparent;
	border-right: 7.5px solid transparent;
	border-bottom: 9px solid #fff; }
passive {
	background: transparent;
	width: 15px;
	height: 9px;
	border-left: 7.5px solid transparent;
	border-right: 7.5px solid transparent;
	border-top: 9px solid rgba(17,18,39,.4); }
