$maxWidth: 1920;

@mixin adaptiv-font($pcSize, $mobSize) {
	$addSize: $pcSize - $mobSize;
	$maxWidth: $maxWidth - 320;
	font-size: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth})); }

@mixin width-procent($widthPx, $widthFull) {
	width: (($widthPx / $widthFull) * 100%)+0.35; }

.stock {
	& .stock__fix-container {
		@include width-procent(1170, 1920);
		& .stock__title {
			width: fit-content;
			color: #ffffff;
			font-size: 22px;
			font-weight: 600;
			line-height: 40px;
			letter-spacing: 1.5px;
			background-color: #0e539a;
			padding: 0 20px; }
		& .stock__offers {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			margin: 25px 0;
			& .offers__item {
				height: 377px;
				@include width-procent(360, 1170);
				position: relative;
				box-shadow: 0 0 5px rgba(0, 0, 0, 0.13);
				padding: 0 0 20px 0;
				& .item__status {
					color: #ffffff;
					font-size: 18px;
					font-weight: 400;
					line-height: 28px;
					letter-spacing: 0.18px;
					background-color: #28bd49;
					padding: 10px 20px;
					position: absolute;
					top: 0;
					right: 0; }
				& .item__img {
					width: 100%;
					height: 57.02%;
					object-fit: fill; }
				& .item__inform {
					padding: 20px 20px 0 20px;
					& .item__title {
						color: #111227;
						font-size: 24px;
						font-weight: 700;
						line-height: 40px;
						letter-spacing: 0.72px; }
					& .item__characteristics {
						display: flex;
						justify-content: space-between;
						margin: 10px 0;
						& .characteristics__item {
							width: fit-content;
							color: #111227;
							font-size: 16px;
							font-weight: 600;
							line-height: 28px;
							background-color: #f3f3f3;
							padding: 2px 20px; } }
					& .item__price {
						color: #28bd49;
						font-size: 24px;
						font-weight: 600;
						line-height: 52.79px;
						letter-spacing: 0.18px; } } } } } }
