$maxWidth: 1920;

@mixin adaptiv-font($pcSize, $mobSize) {
	$addSize: $pcSize - $mobSize;
	$maxWidth: $maxWidth - 320;
	font-size: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth})); }

@mixin width-procent($widthPx, $widthFull) {
	width: (($widthPx / $widthFull) * 100%)+0.35; }

.options {
	& .options__title {
		color: #ffffff;
		font-size: 18px;
		font-weight: 700;
		line-height: 40px;
		letter-spacing: 0.54px;
		background-color: #0e539a;
		text-align: center;
		margin: 0 0 20px 0; }
	& .options__tariffs {
		margin: 0 0 20px 0;
		& .tariffs__title {
			color: #111227;
			font-size: 24px;
			font-weight: 600;
			line-height: 40px;
			letter-spacing: 0.72px; }
		& .tariffs__list {
			& .list__item {
				display: flex;
				justify-content: space-between;
				align-items: center;
