						font-size: 18px;
						font-weight: 400;
						line-height: 40px;
						letter-spacing: 0.54px; }
					& .name__item.bold {
						color: #111227;
						font-size: 18px;
						font-weight: 600;
						line-height: 40px;
						letter-spacing: 0.54px; }
					& .name__img {
						margin: 0 0 0 10px; } }
				& .item__attribute {
					color: #111227;
					font-size: 18px;
					font-weight: 600;
					line-height: 40px;
					letter-spacing: 0.54px; }
				& .item__img {
					@include width-procent(23, 370); }
