$maxWidth: 1920;

@mixin adaptiv-font($pcSize, $mobSize) {
	$addSize: $pcSize - $mobSize;
	$maxWidth: $maxWidth - 320;
	font-size: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth})); }

@mixin width-procent($widthPx, $widthFull) {
	width: (($widthPx / $widthFull) * 100%)+0.35; }

.numeric {
	height: 287px;
	& .numeric__fix-container {
		@include width-procent(1170, 1920);
		background: url('/img/about__img2.png') center/cover no-repeat;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-evenly;
		& .numeric__title {
			color: #ffffff;
			font-size: 26px;
			font-weight: 700;
			line-height: 31.91px;
			text-transform: uppercase; }
		& .numeric__text-block {
			@include width-procent(934, 1185);
			display: flex;
			justify-content: space-between;
			& .text-block__item {
				display: flex;
				align-items: center;
				& .item__num {
					color: #ffffff;
					font-size: 90px;
					font-weight: 900;
					line-height: 40.2px;
					letter-spacing: 2.7px;
					margin: 0 25px 0 0; }
				& .item__subtitle {
					color: #ffffff;
					font-size: 18px;
					font-weight: 400;
					line-height: 22.11px;
					letter-spacing: 0.54px;
					position: relative;
					&:before {
						content: '';
						padding: 1.5px 50%;
						position: absolute;
						background: #167fea;
						top: -9px; } } } } } }
