$maxWidth: 1920;

@mixin adaptiv-font($pcSize, $mobSize) {
	$addSize: $pcSize - $mobSize;
	$maxWidth: $maxWidth - 320;
	font-size: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth})); }

@mixin width-procent($widthPx, $widthFull) {
	width: (($widthPx / $widthFull) * 100%)+0.35; }

.header {
	height: 157px;
	& .header__contacts {
		height: 98px;
		& .header__fix-container {
			display: flex;
			justify-content: space-between;
			align-items: center;
			& .header__logo {
				@include width-procent(230, 1370);
				display: flex;
				align-items: center;
				justify-content: space-between;
				& .logo__img {}
				& .header__logo-text {
					display: flex;
					flex-direction: column;
					& .logo-text__item {
						text-transform: uppercase;
						font-size: 20px;
						font-weight: 700;
						line-height: 24px;
						letter-spacing: 2px;
						color: #0e429a; } } }
			& .header__address {
				@include width-procent(289, 1370);
				font-size: 18px;
				line-height: 24px;
				letter-spacing: 0.36px;
				color: #111227; }
			& .header__work-hours {
				@include width-procent(145, 1370);
				& .working-hours__item {
					font-size: 18px;
					line-height: 24px;
					letter-spacing: 0.36px;
					color: #111227; } }
			& .header__social-list {
				display: flex;
				justify-content: space-between;
				@include width-procent(215, 1370);
				& .social-list__item {
					height: 35px;
					width: 35px;
					background-repeat: no-repeat;
					background-size: contain; }
				& .vk-btn {
					background-image: url('/img/vk-btn.png'); }
				& .inst-btn {
					background-image: url('/img/inst-btn.png'); }
				& .fb-btn {
					background-image: url('/img/fb-btn.png'); }
				& .od-btn {
					background-image: url('/img/od-btn.png'); }
				& .tw-btn {
					background-image: url('/img/tw-btn.png'); } }
			& .header__phone-contacts {
				@include width-procent(296, 1370);
				& .header__phone-number {
					text-align: end;
					& .phone-number__item {
						font-size: 18px;
						font-weight: 700;
						letter-spacing: 0.18px;
						color: #05080c; }
					& .item1 {
						margin: 0 14px 0 0; } }
				& .header__chats {
					text-align: end;
					font-style: italic;
					padding: 8px 0 0 0;
					& .chats__item {
						font-size: 14px;
						font-weight: 700;
						text-transform: uppercase;
						letter-spacing: 0.28px;
						text-decoration: underline;
						text-underline-position: under;
						text-decoration-style: dashed; }
					& .telegram {
						color: #0e539a; }
					& .viber {
						color: #7c3cae;
						margin: 0 8px; }
					& .whatsapp {
						color: #2ad94d; } } } } }
	& .header__navigation {
		background-color: #f5f5f5;
		height: 59px;
		& .header__fix-container {
			display: flex;
			& .header__nav-list {
				@include width-procent(1168, 1370);
				margin: 0 auto;
				height: 100%;
				display: flex;
				justify-content: space-between;
				align-items: center;
				& .nav-list__item {
					font-size: 19px;
					font-weight: 500;
					line-height: 11.82px;
					letter-spacing: 0.38px;
					& a {
						color: #111227; } } } } } }
