* {
	font-family: 'ProbaPro', sans-serif;
	padding: 0;
	margin: 0;
	border: 0; }

*,*:before,*:after {
	box-sizing: border-box; }

html,body {
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1; }

nav,footer,header,aside {
	display: block; }

input,button,textarea {
	font-family: inherit; }

button {
	cursor: pointer; }

a, a:visited, a:hover {
	text-decoration: none; }

ul li {
	list-style: none; }

h1,h2,h3,h4,h5,h6 {
	font-size: inherit;
	font-weight: 400; }

img {
	max-width: 100%; }

*[class$="fix-container"] {
	max-width: 1370px;
	min-width: 320px;
	margin: 0 auto;
	height: 100%;
	@media (max-width: 1370px) {
		max-width: 970px; }
	@media (max-width: 992px) {
		max-width: 750px; }
	@media (max-width: 767px) {
		max-width: none;
		padding: 0 15px; } }

*[class*="font-bebas"] {
	font-family: 'BebasNeue', sans-serif;
	& * {
		font-family: 'BebasNeue', sans-serif; } }

*[class*="font-ptsans"] {
	font-family: 'PTSans', sans-serif;
	& * {
		font-family: 'PTSans', sans-serif; } }


@mixin adaptiv-font($pcSize, $mobSize) {
	$addSize: $pcSize - $mobSize;
	$maxWidth: $maxWidth - 320;
	font-size: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth})); }

@mixin width-procent($widthPx, $widthFull) {
	width: (($widthPx / $widthFull) * 100%)+0.35; }

.page-informtion {
	margin: 0 0 45px 0;
	& .page-informtion__fix-container {
		@include width-procent(1170, 1920);
		& .page-informtion__breadscrumbs {
			margin: 15px 0 0 0;
			letter-spacing: 1.5px;
			& .page-informtion__breadscrumbs-list {
				display: flex;
				& .breadscrumbs-list__item {
					font-size: 16px;
					font-weight: 400;
					line-height: 40px;
					color: #848484;
					padding: 0 0 0 20px;
					position: relative;
					&:before {
						content: url('/img/breadscrumbs.png');
						position: absolute;
						bottom: 3px;
						left: 8px; } }
				& .home {
					color: #111227;
					text-decoration: underline;
					text-underline-position: under;
					text-decoration-color: rgba(17, 18, 39,.25);
					&:nth-child(1) {
						padding: 0;
						&:before {
							content: none; } } } } }
		& .page-informtion__page-name {
			color: #111227;
			font-size: 36px;
			font-weight: 900;
			line-height: 40px;
			letter-spacing: 1.08px;
			position: relative;
			padding: 0 0 0 26px;
			margin: 14px 0 0 0;
			border-left: 6px solid #0e539a; }
		& .page-informtion__page-subname {
			opacity: 0.6;
			color: #111227;
			font-size: 18px;
			font-weight: 600;
			line-height: 40px;
			letter-spacing: 0.54px; } } }
.map {
	position: relative;
	& .map__toggle-btn {
		width: 287px;
		height: 67px;
		background-color: #0e539a;
		color: #fff;
		font-size: 24px;
		font-weight: 400;
		line-height: 13.66px;
		letter-spacing: 1.57px;
		text-transform: uppercase;
		position: absolute;
		top: 0;
		left: 50%;
		transform: translate(-50%,-50%);
		z-index: 2; } }
