.page-preview {
	width: 100%;
	height: 260px;
	background: rgba(0,0,0,1);
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	align-items: center;
	& .page-preview__mask {
		height: 100%;
		width: 100%;
		background: url('/img/card__img1.png') no-repeat;
		position: absolute;
		top: 0;
		left: 0; }
	& .page-preview__title {
		color: #ffffff;
		font-size: 48px;
		font-weight: 400;
		line-height: 11.86px;
		text-transform: uppercase;
		letter-spacing: 1.44px;
		z-index: 2;
		margin: 0 0 40px 0; }
	& .page-preview__chats {
		width: 155px;
		display: flex;
		justify-content: space-between;
		z-index: 2;
		& .chat__item {
			height: 35px;
			width: 35px;
			background-color: transparent;
			& img {
				width: 100%; } } } }
