@mixin width-procent($widthPx, $widthFull) {
	width: (($widthPx / $widthFull) * 100%); }

.columns {
	& .columns__fix-container {
		@include width-procent(1170, 1920);
		display: flex;
		justify-content: space-between;
		& .columns__col-left {
			@include width-procent(770, 1170); }
		& .columns__col-right {
			@include width-procent(370, 1170); } } }
